import React from 'react';
import {
  TabGap,
  TabItem,
  TabLevel,
  TabsWidth
} from 'core/types/tabs';
import { cssDecorator } from '@front-toolkit/utils';

import { Text } from '../typography';

import styles from './Tab.scss';

const tabsElementIdToIdentify = 'tabsElementIdToIdentify';

const cd = cssDecorator(styles);

interface TabProps extends TabItem {
  /** Колбэк нажатия на таб */
  onClick: (
    event: React.MouseEvent<HTMLButtonElement>,
    value: string,
    disabled: boolean
  ) => void;

  /** Флаг активной вкладки */
  isActive: boolean;

  /** Тип блока вкладки для отображения */
  width?: TabsWidth;

  /** Высотка вкладки */
  level?: TabLevel;

  /** Размер отступов между вкладками */
  gap?: TabGap;
}

function Tab({
  label,
  value,
  disabled = false,
  dataTestId,
  counter,
  iconRight,
  isActive,
  width,
  gap = 'default',
  level = 'level1',
  onClick
}: TabProps): JSX.Element {
  const tabStyles = cd(
    'tab',
    {
      [level]: true,
      [`${ level }_${ gap }`]: true,
      active: isActive,
      disabled,
      fill: width === TabsWidth.Fill
    }
  );

  return (
    <button
      type="button"
      className={ tabStyles }
      onClick={ (event: React.MouseEvent<HTMLButtonElement>): void => onClick(event, value, disabled) }
      data-test-id={ dataTestId }
      disabled={ disabled }
    >
      <Text
        color="inherit"
        size="small"
        fontWeight="inherit"
        title={ label }
      >
        { label }
      </Text>
      { Boolean(counter || counter === 0) && <Text color="secondary" size="small">{ counter }</Text> }
      { Boolean(iconRight) && <div id={ tabsElementIdToIdentify }>{ iconRight }</div> }
    </button>
  );
}

export { tabsElementIdToIdentify };

export default React.memo(Tab);
